
  .containertext1 {
font-family: ui-sans-serif;
color:whitesmoke;
width: 100vh;
text-align: center;
/* margin-top: 5%;
margin-bottom: 8%; */
  }
  @media (max-width: 1331px) {
   .containertext1 {
    width: 90%;
   }
}
/* Contenitore principale */
.containertext1 {
  overflow: hidden;
}

/* Animazione base */
.slide-down,
.slide-left-to-right,
.slide-right-to-left {
  opacity: 0;
  transition: opacity 1.5s ease-out, transform 1.5s ease-out;
}

/* Dall'alto verso il basso */
.slide-down {
  transform: translateY(-100%); /* Posizione iniziale fuori dallo schermo in alto */
}

.slide-down.visible {
  opacity: 1;
  transform: translateY(0); /* Torna alla posizione originale */
}
.slide-up {
  opacity: 0;
  transform: translateY(50px);
  transition: opacity 0.8s ease, transform 0.8s ease;
}

.slide-up.visible {
  opacity: 1;
  transform: translateY(0);
}

/* Da sinistra verso destra */
.slide-left-to-right {
  transform: translateX(-100%); /* Posizione iniziale fuori dallo schermo a sinistra */
}

.slide-left-to-right.visible {
  opacity: 1;
  transform: translateX(0);
}

/* Da destra verso sinistra */
.slide-right-to-left {
  transform: translateX(100%); /* Posizione iniziale fuori dallo schermo a destra */
}

.slide-right-to-left.visible {
  opacity: 1;
  transform: translateX(0);
}
