.container {

    background-image: url('../../../assets/prova2.avif');
    background-size: cover;
    background-repeat: no-repeat;
    padding: 50px;
    text-align: center;
    color: white;
    font-family: ui-sans-serif;
    /* height: 100vh; */
}

.title {
    font-family: 'ui-sans-serif';
    font-size: 7em;
    margin-bottom: -0.4em;

}

.sectionTitle {
    width: 80em;
    margin-inline: auto;
    margin-top: 5em;
}
.subtitle {
    font-family: 'ui-sans-serif';
    font-size: 2.6em;
    margin-top: 20px;
    text-align: center;
    width: 30em;
}

.text {
    font-family: 'ui-sans-serif';
    margin-top: 20px;
    line-height: 1.5;
    text-align: center;
    font-size: 2em;
}
.carousel-container {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
  }
  
  .single-carousel {
    flex: 1;
    min-width: 300px;
    overflow: hidden;
    position: relative;
  }
  
  .carousel-inner {
    display: flex;
    transition: transform 0.5s ease-in-out;
    width: calc(300px * 5); /* 5 immagini per carosello */
  }
  
  .carousel-item {
    min-width: 300px;
    flex-shrink: 0;
  }
  
  @media (max-width: 768px) {
    .carousel-container {
      flex-direction: column;
      gap: 24px;
    }
  }
  
@media (max-width: 1331px) {
    .container {
        height: auto;
        font-family: 'ui-sans-serif';
        padding: 1%;
    }
    .text {
        font-size: 1.4em;
    }
    .sectionTitle {
        width: 21.8em;
    }
    .title {
        font-family: 'ui-sans-serif';
        font-size: 4em;
    }

    .subtitle {
        font-family: 'ui-sans-serif';
        font-size: 1.5em;
        width: auto;
    }
}
.carousels-container {
    margin-bottom: 6em;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 20px;
}
.slide-down{
  opacity: 0;
  transition: opacity 1.5s ease-out, transform 1.5s ease-out;
}

/* Dall'alto verso il basso */
.slide-down {
  transform: translateY(-100%); /* Posizione iniziale fuori dallo schermo in alto */
}

.slide-down.visible {
  opacity: 1;
  transform: translateY(0); /* Torna alla posizione originale */
}
.carousel {
  width: 350px;
  height: 350px;
  /* overflow: hidden; */
  margin: 20px;
}

.carousel img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 768px) {
  .carousels-container {
    flex-direction: column;
    align-items: center;
  }
  .carousel {
    width: 300px;
    height: 350px;
    /* overflow: hidden; */
    margin: 20px;
  }
  .carousel img {
    height: 80%;
  }
}
.caption {
    font-size: 14px;
    color: white;
    margin-top: 10px;
  }
/* for services */
.services-outer-container {
    background-color: #1F2A44;
    /* Imposta il colore di sfondo desiderato */
    width: 100vw;
    overflow-x: hidden;
    /* Evita scrollbar orizzontali indesiderati */
}

.services-container {
    padding: 20px;
    font-family: ui-sans-serif;
   /*  max-width: 800px; */
    margin: auto;
    color: white;
    width: 900px;
}

.service {
    margin-bottom: 30px;
}

.service h2 {
    font-size: 40px;
    margin-bottom: 10px;
}

.service p {
    font-size: 30px;
    line-height: 1.5;
}

@media (max-width: 1331px) {
    .services-container {
        padding: 10px;
        max-width: 800px;
        width: auto;
    }

    .service h2 {
        font-size: 20px;
    }

    .service p {
        text-align: left;
        font-size: 14px;
    }
}

/* for the image */
.tecimage {
    width: 100%;
    margin-left: -4px;
}

@media (max-width: 1331px) {
    .tecimage {
        width: 100%;
    }
}

/* for the button */
.contact {
    text-align: center;
    width: 20em;
    height: 4em;
    font-family: ui-sans-serif;

    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
}