.App {
  /*  min-height: 100vh; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
/*   background-color: rgb(92, 91, 91); */
  background-color: black;
  overflow: hidden;
}

.header-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}

body::-webkit-scrollbar {
  width: 0;
  /* Nasconde la barra di scorrimento su browser Webkit come Chrome e Safari */
}

body {
  -ms-overflow-style: none;
  /* Nasconde la barra di scorrimento su browser basati su IE e Edge */
}
.background-class {
  margin: 0;
  text-align: center;
  background-image: url('../../assets/backgroun2.jpg'); /* Cambia il percorso se necessario */
  background-position: 50%;
  background-repeat: repeat-y;
  background-size: auto;
  background-attachment: fixed;
  width: 100%;
   height: 100%; 
  margin-top: -5%;
  position: relative;
  margin-top: 10vh;
}
.background-classAbout {
  margin: 0;
  text-align: center;
  background-image: url('../../assets/backgroun2.jpg'); /* Cambia il percorso se necessario */
  background-position: 50%;
  background-repeat: repeat-y;
  background-size: cover;
  background-attachment: fixed;
  width: 100%;
 /*   height: 100%; 
  margin-top: -5%;
  position: relative; */
}
.slide-down-home {
  transform: translateY(-180px); /* Partenza da una posizione pi√π bassa */
  opacity: 0; /* Inizia invisibile */
  transition: all 1.2s ease-out;
}

.slide-down-home.visible {
  transform: translateY(0); /* Arriva nella posizione finale */
  opacity: 1; /* Diventa visibile */
}
@media (max-width: 1331px) {
.background-class {
   height: 100%; 
}
}
